.main-menu-links {
  hr {
    background: var(--border-color);
  }

  .nav-list {
    .e-listbox-wrapper .e-list-item:hover,
    .e-listbox-wrapper .e-list-item.e-selected {
      background-color: transparent;
    }

    .nav-item {
      &:hover {
        background-color: var(--springtime-rain);
      }

      &.nav-item-active {
        background-color: var(--aqua);

        .nav-link {
          .icon,
          .text,
          .favorite {
            color: var(--white);
          }
        }
      }

      .nav-link {
        color: var(--black);

        .icon {
          color: var(--warnerblue);

          img[src*="svg"] {
            filter: var(--warnerblue-filter);
          }
        }
      }

      [title$="Admin"] span.icon:after {
        color: var(--warnerblue);
      }
    }
  }

  .tool-group-name {
    color: var(--aqua);
  }
}
