.e-richtexteditor {
  .e-rte-content {
    .e-content p {
      font-size: 15px;
    }
  }
}

.rich-text-editor-with-file {
  .e-content {
    counter-reset: files;

    [file] {
      color: blue;
      text-decoration: underline;
      counter-increment: files;

      &::after {
        content: counter(files);
        font-size: 10px;
        vertical-align: top;
      }
    }
  }
}

.e-rte-inline-popup.e-popup-open {
  z-index: 9999 !important;
}
