.main-menu {
  .wrapper {
    background-color: var(--midnight);
    color: var(--white);

    .resource-cta {
      border: 1px solid var(--white15);

      .icon {
        color: var(--marine);
      }

      .text a {
        color: var(--sunflower);
      }
    }

    hr {
      background: var(--white50);
    }

    .theme-switch-container {
      .ng-fa-icon {
        color: var(--marine);
      }

      .e-switch-inner.e-switch-active,
      .e-switch-on {
        background-color: var(--marine-transparent);
      }
    }

    .wrapper-links {
      &::-webkit-scrollbar-thumb {
        box-shadow: inset 0 0 6px var(--white50);
      }
    }
  }
}
