.e-btn {
  color: #666666;
  background-color: #F5F5F5;
  border-color: #F5F5F5;
  border-radius: 3px;
  font-size: 14px;
}

.e-btn.e-dark {
  background-color: #666666;
  border-color: transparent;
  color: #fff;

  &:active,
  &:hover {
    background-color: #878585;
    border-color: transparent;
    color: #fff;
  }
}
