.confirm-dialog-container {
  .swal2-popup {
    padding: 0.5em 0.5em 2em 0.5em;
    width: 35em;

    .swal2-title {
      padding-top: 2em;
    }

    .acknowledge-action {
      height: 60px;
      background: var(--acknowledge-background);
      border-radius: 5px;
      margin: 2em 1em 0em 1em;
    }

    .swal2-validation-message {
      margin: 1em 1em 0em 1em;
    }

    .swal2-actions {
      margin: 2em 0 0 0;

      button {
        min-width: 12em;
        height: 35px;
        padding: 0;
        font-size: 14px;
        margin-right: 1em;
      }
    }
  }
}
