// Font Imports
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

// Variables
:root {
  --montserrat: 'Montserrat', sans-serif;
  --black: #333333;
  --graydark: #666666;
  --graymedium: #999999;
  --graylight: #CCCCCC;
  --white: #ffffff;
  --white-filter: brightness(0) invert(1);
  --white50: rgba(255,255,255, 0.5);
  --white50-filter: brightness(0) invert(1) opacity(0.5);
  --white15: rgba(255,255,255, 0.15);
  --warnerblue: #1B3E6F;
  --warnerblue-filter: invert(22%) sepia(24%) saturate(1696%) hue-rotate(176deg) brightness(94%) contrast(96%);
  --border-color: #E5E5E5;
  --warnerblue5: rgba(27,62,111, 0.05);
  --midnight: #0F2D52;
  --twilight: #104D97;
  --marine: #49CDC6;
  --marine-transparent: #FFFFFF26;
  --aqua: #00A39A;
  --aqua-active: #00cbc0;
  --aqualight: #E5F6F5;
  --sunflower: #FFD900;
  --accordion-height: 70px;
  --red: #dc3545;
  --redlight: #FBEAEC;
  --indigo-white: #F2FAFA;
  --springtime-rain: #F3F5F8;
  --white-smoke: #F5F5F5;
  --yellow: #FFC107;
  --yellowlight: #FFF9E6;
  --yellow-transparent: #FFD9001A;
  --green: #28A745;
  --greenlight: #E9F6EC;
  --blue: #3E5B84;
  --bluelight: #E8EBF0;
  --acknowledge-background: #DC35450D 0% 0% no-repeat padding-box;
  --shadow-light: rgba(51, 51, 51, 0.1);
}

// Body
html,
body {
  margin: 0;
  padding: 0;
}

*,
.e-control {
  font-family: var(--montserrat);
}

body {
  font-size: 16px;
  color: var(--black);

  &.body-mobile:has(ejs-sidebar.e-open) {
    overflow: hidden;
  }
}

// Text
h1 {
  font-size: 32px;
  font-weight: 600;
}

h2 {
  font-size: 24px;
  line-height: 40px;
  font-weight: 600;
}

h3 {
  font-size: 20px;
  line-height: 36px;
}

h4 {
  font-size: 16px;
  line-height: 32px;
}

h5 {
  font-size: 12px;
  line-height: 28px;
}

p {
  font-size: 16px;
}

.default-text {
  font-size: 14px;
}

.small-text {
  font-size: 12px;
}

// Button
button {
  min-height: 35px;

  &.e-success {
    background-color: var(--aqua) !important;
    border-color: var(--aqua) !important;

    &:active,
    &:hover {
      background-color: var(--aqua-active) !important;
      border-color: var(--aqua-active) !important;
    }
  }
}

.btn-default-size {
  width: 200px;
}

.e-card-content + .actions-container {
  padding-right: 10px;
}

.actions-container button {
  width: 100%;
}

.actions-container input {
  width: 100%;
}

// Shadow
.shadow {
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px !important;
}

// Border
.rounded-borders {
  border-radius: 10px;
}

// Container
.border-box {
  box-sizing: border-box;
}

.ml {
  &-0 {
    margin-left: 0 !important;
  }

  &-1 {
    margin-left: .25rem !important;
  }

  &-2 {
    margin-left: .5rem !important;
  }

  &-3 {
    margin-left: 1.0rem !important;
  }

  &-4 {
    margin-left: 1.5rem !important;
  }
}

.mr {
  &-0 {
    margin-right: 0 !important;
  }

  &-1 {
    margin-right: .25rem !important;
  }

  &-2 {
    margin-right: .5rem !important;
  }

  &-3 {
    margin-right: 1.0rem !important;
  }

  &-4 {
    margin-right: 1.5rem !important;
  }
}

mt {
  &-auto {
    margin-top: auto;
  }
}

.pl {
  &-0 {
    padding-left: 0 !important;
  }

  &-1 {
    padding-left: .25rem !important;
  }

  &-2 {
    padding-left: .5rem !important;
  }

  &-3 {
    padding-left: 1.0rem !important;
  }

  &-4 {
    padding-left: 1.5rem !important;
  }
}

.pr {
  &-0 {
    padding-right: 0 !important;
  }

  &-1 {
    padding-right: .25rem !important;
  }

  &-2 {
    padding-right: .5rem !important;
  }

  &-3 {
    padding-right: 1.0rem !important;
  }

  &-4 {
    padding-right: 1.5rem !important;
  }
}

.e-dark {
  background-color: #666666;
  border-color: transparent;
  color: #fff;

  &:active,
  &:hover {
    background-color: #878585;
    border-color: transparent;
    color: #fff;
  }
}

textarea.default-textarea.e-control {
  resize: none;
  height: 70px;
}

.red-color {
  color: var(--red);
}

.e-control[required] label:after {
  content: " *";
}

.custom-icon-wrapper {
  color: var(--warnerblue);
  font-size: 25px;
  background-color: var(--warnerblue5);
  border-radius: 10px;
  height: 50px;
  width: 50px;
  display: flex;
  align-items: center;
  justify-content: center;

  &.dark-background {
    background-color: var(--warnerblue);
    color: var(--white);
  }

  img {
    height: 30px;
    width: 30px;
  }
}

.align-middle {
  display: flex;
  align-items: center;
}

.align-middle-vh {
  display: flex;
  align-items: center;
  height: 100vh;
  justify-content: center;
}

.align-middle-space-between {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.fd-c {
  flex-direction: column;
}

.pos-abs-top {
  position: absolute;
  top: -3em;
}

a.blue-link {
  color: var(--blue);
  cursor: pointer;
  text-decoration: none;
  display: flex;
  justify-content: flex-start;
  align-items: center;

  > * {
    cursor: pointer;
  }
}

.fd-c {
  flex-direction: column;
}

.pos-abs-top {
  position: absolute;
  top: -3em;
}

ul.custom-tree-view {
  padding-left: 0;

  ul {
    padding-left: 15px;
    margin: 0.25em 0;
  }
  // SubCategory
  ul.custom-tree-view {
    // Question
    ul.custom-tree-view {
      // Answer
      ul li {
        border-left: 1px solid var(--border-color);
      }
    }
  }
}

.gray-action {
  cursor: pointer;
  font-size: 14px;
  line-height: 14px;
  color: var(--graymedium);
  background-color: var(--white);
}
