.e-accordion {
  padding: 0 10px;

  .e-acrdn-item,
  .e-acrdn-item.e-selected {
    &.e-select.e-expand-state > .e-acrdn-header:focus,
    .e-acrdn-header:focus,
    .e-acrdn-header:active,
    .e-acrdn-header:focus,
    .e-acrdn-header {
      background: none;
      border: none;
      box-shadow: none;
    }

    .e-acrdn-header {
      height: var(--accordion-height);

      .e-toggle-icon {
        height: var(--accordion-height);
      }

      .e-acrdn-header-content {
        height: 100%;
        color: var(--warnerblue);
        font-size: 20px;

        > div {
          height: 100%;
          align-items: center;
        }
      }
    }
  }
}
