.e-listbox-wrapper.e-sortableclone {
  .e-list-item {
    background-color: var(--white);
    border-radius: 10px;
    max-height: 45px;
  }

  .icon,
  .text {
    color: black;
  }

  img {
    max-height: 30px;
    width: 30px;
    margin-right: 0.5em;
    -webkit-user-drag: none;
  }
}
