.e-treeview {
  .e-list-item {
    &.e-hover > .e-fullrow {
      background-color: transparent;
      border-color: transparent;
    }

    &.e-active > .e-text-content,
    &.e-hover > .e-text-content {
      background-color: var(--white-smoke);
      border-color: var(--white-smoke);
    }

    &.e-active {
      .e-list-item {
        background-color: var(--gray-light);
        border-color: var(--gray-light);
      }

      > .e-fullrow, &.e-hover > .e-fullrow {
        display: none;
      }

      > .e-text-content .e-icon-collapsible,
      &.e-hover > .e-text-content .e-icon-collapsible,
      > .e-text-content .e-icon-expandable,
      &.e-hover > .e-text-content .e-icon-expandable,
      > .e-text-content .e-list-text,
      &.e-hover > .e-text-content .e-list-text {
        color: var(--black);
      }
    }
  }
}
