.e-checkbox-wrapper {
  .e-frame.e-check,
  .e-checkbox:active + .e-frame.e-check,
  .e-checkbox:focus + .e-frame.e-check {
    background-color: var(--aqua);
    border-color: var(--aqua);
  }

  &:hover .e-frame.e-check {
    background-color: var(--aqua-active);
    border-color: var(--aqua-active);
  }

  .e-checkbox:active + .e-frame,
  .e-checkbox:focus + .e-frame {
    border-color: var(--aqua-active) !important;
  }

  .e-label {
    font-family: var(--montserrat);
  }
}
