$rotate: rotate(45deg);

ejs-grid.e-grid {
  display: flex;
  flex-direction: column;
  min-height: 400px;

  .action-button {
    width: 100px;
    margin-right: 5px;
    border-radius: 3px;
    text-transform: capitalize;
  }

  thead th:first-child .e-icon-filter {
    display: none;
  }

  img {
    height: 30px;
  }

  .e-sortfilter thead th.e-fltr-icon .e-sortfilterdiv:not(.e-icon-ascending):not(.e-icon-descending)::before {
    content: "\e824";
  }

  .e-icon-rowdragicon::before,
  .e-icon-rowdragicon::before {
    content: "\e838" !important;
    font-size: 12px !important;
    -webkit-transform: $rotate;
    -moz-transform: $rotate;
    -o-transform: $rotate;
  }

  .e-gridpager {
    margin-top: auto;
  }
}
