ejs-splitbutton.fw-split {
  width: 100% !important;

  > button:first-child {
    flex: 5;
  }

  > button.e-icon-btn {
    border-left: 1px solid var(--white) !important;
    flex: 1;

    span.e-btn-icon:before {
      content: '\e729' !important;
      font-weight: bold;
    }
  }
}
