.main-menu-links {
  hr {
    background: var(--white50);
  }

  .nav-list {
    .e-listbox-wrapper .e-list-item:hover,
    .e-listbox-wrapper .e-list-item.e-selected {
      background-color: transparent;
    }

    .nav-item {
      &:hover {
        background-color: var(--white15);

        .nav-link .icon {
          color: var(--white);

          &:after {
            color: var(--white);
          }

          img[src*="svg"] {
            filter: var(--white-filter);
          }
        }
      }

      &.nav-item-active {
        background-color: var(--twilight);
      }

      .nav-link {
        color: var(--white);

        .icon {
          color: var(--white50);

          &:after {
            color: var(--white50);
          }

          img[src*="svg"] {
            filter: var(--white50-filter);
          }
        }
      }
    }
  }

  .tool-group-name {
    color: var(--marine);
  }
}
