// Card
.e-card {
  box-shadow: 0px 3px 6px var(--shadow-light);
  border-radius: 15px;
  border-color: var(--border-color);

  .e-card-header .e-card-content {
    color: var(--black);
  }

  .e-card-header {
    justify-content: space-between;
    align-items: center;

    ejs-textbox {
      max-width: 300px;
      width: 100%;
      margin-right: 1em;
    }

    button {
      min-width: 180px;
    }

    .e-card-header-caption .e-card-sub-title {
      white-space: normal;

      label {
        cursor: pointer;
      }
    }
  }
}

// Card List
.e-card {
  &.card-list {
    cursor: pointer;
    height: 100px;
    border-radius: 15px;

    .e-card-header {
      padding-top: 0;
    }

    .e-card-header-title {
      font-size: 20px !important;
      font-weight: 600 !important;
      color: var(--warnerblue);
    }

    .e-card-header-image {
      justify-content: space-between;
      align-items: center;
    }

    .e-card-sub-title {
      font-size: 16px;
    }
  }
}
