.main-menu {
  .wrapper {
    background-color: var(--white);
    color: var(--black);
    border: 1px solid var(--border-color);

    .resource-cta {
      background-color: var(--indigo-white);
      border: 1px solid var(--white15);

      .icon {
        color: var(--aqua);
      }

      .text a {
        color: var(--aqua);
      }
    }

    hr {
      background: var(--border-color);
    }

    .theme-switch-container {
      .ng-fa-icon {
        color: var(--graymedium);
      }

      .e-switch-inner .e-switch-off {
        background-color: var(--springtime-rain);
      }
    }

    .wrapper-links {
      &::-webkit-scrollbar-thumb {
        box-shadow: inset 0 0 6px var(--aqua);
      }
    }
  }
}
